
import Vue from "vue";
import Component from "vue-class-component";
import {Prop} from "vue-property-decorator";
@Component({
  name: "BtnTab",
})
export default class BtnTab extends Vue {
  @Prop({
    type: String,
    default: "link",
  }) type!: "link" | "button";
  @Prop( String) link!: string;
}
